.logo {
	position: relative;
	width: 177.58px;
	height: 35.58px;
}.logo:hover {
	cursor: pointer;
	transform: scale(1.01);
	filter: invert(53%) sepia(100%) saturate(3103%) hue-rotate(166deg) brightness(100%) contrast(86%);
}
.navbar {
	height:80px;
	z-index: 2;
	position: absolute;
	width:100vw;
}
.navigation-button{
	position: absolute;
	width: 111px;
	height: 40px;
	top:19px;
	background: #2D9CDB;
	border-radius: 3px;
	border: none;
	cursor: pointer;
	font-family: Roboto;
	font-style: normal;
	font-weight: bold;
	line-height: normal;
	font-size: 14px;
	color: #FFFFFF;
}.navigation-button:hover{
    background: #2D9CDB;
}.navigation-button:active{
    box-shadow:inset 0 0 5px  #005987;
}

.navigation-link{
	/*position: relative;*/
	font-family: Roboto;
	font-style: normal;
	font-weight: bold;
	line-height: normal;
	font-size: 14px;
	color: #FFFFFF;
	/*margin-left: 19px;*/
	/*margin-right: 19px;*/
}.navigation-link:hover{
	color: #12A3DD;
	text-decoration: none;
}
@media (min-width: 320px) and (max-width: 991px){
	.logo {
		filter: invert(53%) sepia(100%) saturate(3103%) hue-rotate(166deg) brightness(100%) contrast(86%);
	}
	.navigation-link {
		color: black;
	}
	.navigation-button{
		position: relative;
	    left: 10px;
	    width: calc(100vw - 20px);
	    /*top:0px;*/
	    margin-bottom:0px;
	}
	.navbar-collapse{
		background-color: white;
	    width: 100vw;
	    left: 0px;
	    position: absolute;
	    top: 80px;
	}
	.navbar {
		background-color: white !important;
		    border: none;
	}
	.navbar-nav{
	    height: 50vh;
	}
	.navbar-block-item{
		position: relative;
	}
	.navbar-item {
		position: relative;
		display: flex;
		flex-direction: column;
	}
}

@media (min-width: 992px) and (max-width: 1399px){
	.logo {
		left: 20px;
	}
	.navigation-button{
		right:50px;
	}
	.navbar-block-item {
		display: flex;
		flex-direction: row;
		position: relative;
		left: 64px;
	    width: calc(100vw - 468px);
	    justify-content: space-between;
	}
}

@media (min-width: 1400px){
	.logo {
		left: 120px;
	}

	.navigation-button{
		right:130px;
	}
	.navbar-block-item {
		display: flex;
		flex-direction: row;
		position: relative;
	    left: 172px;
	    width: calc(100vw - 675px);
	    justify-content: space-between;
	}
}

