.getstarted-parallax-container {
	margin-top: 100px;
    position: relative;
    width: 100%;
    height: 330px;
    overflow: hidden;

}
.getstarted-parallax-image {
    width: 100vw;
    height: 500px; 
    background-size: cover;
    background-position: center;

}
.getstarted-parallax-children {
	height: 330px;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}
.getstarted-parallax-text {
position: absolute;
width: 700px;
height: 89px;
left: 406px;
font-family: Elsie;
font-style: normal;
font-weight: normal;
line-height: 44px;
font-size: 36px;
text-align: center;
letter-spacing: 0.02em;
text-transform: uppercase;
color: #FFFFFF;
text-shadow: 0px 0px 11px rgba(0, 0, 0, 0.51);
}
.getstarted-button{
    position: absolute;
    width: 175px;
    height: 54px;
    left: 641px;
    margin-top:80px;
    background: #FFFFFF;
    border-radius: 5px;
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    line-height: normal;
    font-size: 14px;
    text-align: center;
    border: none;
    color: #2D9CDB;
}.getstarted-button:hover{
    background: #EFEFEF;
}
.getstarted-button:active{
    box-shadow:inset 0 0 5px  #C3C3C3;
}
