.bestprice-block{
	position: relative;
	height: 800px;
	top: 500px;
}
.bestprice-text {
	margin-top: 120px;
	margin-left: 130px;
	height: 48px;
	font-family: Hiragino Mincho Pro;
	line-height: normal;
	font-size: 32px;
	letter-spacing: 0.02em;
	color: #09293C;
}
.bestprice-descr{
	position: absolute;
	height: 21px;
	left: 130px;
	font-family: Roboto;
	font-style: normal;
	font-weight: 500;
	line-height: 23px;
	font-size: 13px;
	letter-spacing: 0.02em;
	color: #828282;
}
.bestprice-cards-container{
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
    margin-top: 50px;
    left: 130px;
    position: relative;
    width:85vw;
}

.bestprice-card {
	margin-top: 50px;
	margin-right: 22px;
	width: 380px;
	padding-right: 0px;
	height: 280px;
}

.bestprice-card-img-top {
	height: 220px;
	cursor: default;

}
.index__simpleZoom___pAG-d {
	cursor: default !important;
}

.bestprice-image-text {
    position: absolute;
    width: 136px;
    height: 66px;
	margin-top: -66px;
    margin-left: 244px;
    background: rgba(45, 156, 219, 0.6);
	font-family: Roboto;
	font-style: normal;
	font-weight: bold;
	line-height: 32px;
	font-size: 22px;
	text-align: center;
	text-transform: uppercase;
	color: #FFFFFF;
	padding: 20px;

}

.bestprice-card-img-top:hover .bestprice-image-text{
	bottom: -6px;
	opacity: 0;
}


.bestprice-card-title{
	position: absolute;
	width: 239px;
	height: 22px;
    margin-left: -20px;
	font-family: Roboto;
	font-style: normal;
	font-weight: bold;
	line-height: 23px;
	font-size: 16px;
	text-transform: uppercase;
	color: #4F4F4F;
}
.bestprice-card-text{
	position: absolute;
	width: 198px;
	height: 13px;
    margin-left: -20px;
	margin-top: 30px;
	font-family: Roboto;
	font-style: normal;
	font-weight: bold;
	line-height: 18px;
	font-size: 12px;
	text-transform: uppercase;
	color: #4F4F4F;
}

.stars {
	position: absolute;
	width: 120px;
	height: 16px;
    margin-left: 240px;
}
.review-counter {
	position: absolute;
	width: 68px;
	height: 13px;
    margin-left: 240px;
	margin-top: 30px;
	font-family: Roboto;
	font-style: normal;
	font-weight: 500;
	line-height: 18px;
	font-size: 12px;
	text-transform: lowercase;
	color: #BDBDBD;
}

